.place {
  &__photo-conteiner {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
    border-radius: 8px;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    font-weight: bold;
    color: $colorBlue;
    text-decoration: none;

    &--grey {
      text-decoration: none;
      color: #3e3e3e;
    }
  }
}
